import * as React from 'react';
import { Button } from '@material-tailwind/react';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import { LuPhoneCall } from 'react-icons/lu';
import { FiUser, FiInbox, FiLock } from 'react-icons/fi';
import { useGoogleReCaptcha } from 'react-google-recaptcha-v3';
import { rc4_encrypt_object } from '../../../../../utils/encryptions';
import apiService from '../../../../../utils/services';
import sessions from '../../../../../utils/sessions';
import InputWithLogo from '../../../components/InputWithLogo';

export default function RegisterForm() {
  const phoneRegex = /^(?:\+62|0)[2-9][0-9]{7,12}$/;
  const passwordRegex = /^(?=.*[!@#$%^&])(?=.*[A-Z])(?=.*[a-z])\S{6,}$/;
  const navigate = useNavigate();
  const token = sessions.getSessionToken();
  const [isLoading, setIsLoading] = React.useState(false);
  const [passwordIsVisible, setPasswordVisible] = React.useState(true);
  const [confirmPasswordIsVisible, setConfirmPasswordVisible] = React.useState(true);
  const { executeRecaptcha } = useGoogleReCaptcha();
  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);

  const [formData, setFormData] = React.useState({ name: '', phone: '', email: '', confirm_email: '', password: '', confirm_password: '' });
  const [errors, setErrors] = React.useState<{ [key: string]: string | null }>({ name: null, phone: null, email: null, confirm_email: null, password: null, confirm_password: null });

  React.useEffect(() => {
    token && navigate('/dashboard');
  }, []);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setFormData({ ...formData, [name]: value });
    let error = value === '' ? 'This field is required' : null;
    if (name === 'phone') if (!error && !phoneRegex.test(value)) error = 'Invalid phone number';
    if (name === 'confirm_email') if (!error && value !== formData.email) error = 'Emails do not match';
    if (name === 'password') if (!error && !passwordRegex.test(value)) error =
      'Password must be at least 6 characters long and include at least one uppercase letter, one lowercase letter, and one special character';
    if (name === 'confirm_password') if (!error && value !== formData.password) error = 'Passwords do not match';
    setErrors({ ...errors, [name]: error });
  };

  const onSubmit = React.useCallback((event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setIsLoading(true);
    // Check for empty fields and matching fields
    const newErrors = Object.keys(formData).reduce((acc, key) => {
      if (formData[key as keyof typeof formData] === '') {
        acc[key] = 'This field is required';
      };
      return acc;
    }, {} as { [key: string]: string | null });
    if (formData.email !== formData.confirm_email) newErrors.confirm_email = 'Emails do not match';
    if (formData.password !== formData.confirm_password) newErrors.confirm_password = 'Passwords do not match';
    if (!phoneRegex.test(formData.phone)) newErrors.phone = 'Invalid phone number';
    if (formData.phone === '') newErrors.phone = 'This field is required';
    setErrors(newErrors);
    // Check if there are no errors
    if (!Object.values(newErrors).some(error => error !== null)) {
      if (!executeRecaptcha) {
        console.error('Execute recaptcha not yet available');
        return;
      };
      executeRecaptcha('enquiryFormSubmit')
        .then(async (gRecaptchaToken) => {
          // encrypt payload data to BE
          const encryptRegister = rc4_encrypt_object({
            ...formData, provider: 'form', is_company: 0,
            company_id: '', organization_id: '', gRecaptchaToken
          });
          try {
            const response = await apiService.post('/auth/register', { data: encryptRegister });
            successNotify('Register successfully');
            navigate('/login');
            setIsLoading(false);
          } catch (error) {
            errorNotify(error.response.statusText);
            setIsLoading(false);
          };
        });
    } else {
      errorNotify('Form contains errors or is incomplete');
      setIsLoading(false);
    };
  }, [executeRecaptcha, formData]);

  return (
    <form onSubmit={onSubmit} className="flex flex-col gap-4">
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
        <div>
          <InputWithLogo
            label="Full Name"
            id="name"
            name="name"
            type="text"
            placeholder="Enter your company"
            onChange={(e) => handleChange(e)}
            formData={formData}
            errors={errors}
            leftIcon={<FiUser className="pointer-events-none w-5 h-5 absolute top-[85%] transform -translate-y-1/2 left-2" />}
          />
        </div>
        <div>
          <InputWithLogo
            label="No Telephone"
            id="phone"
            name="phone"
            type="number"
            placeholder="Enter your no telephone"
            onChange={(e) => handleChange(e)}
            formData={formData}
            errors={errors}
            leftIcon={<LuPhoneCall className="pointer-events-none w-5 h-5 absolute top-[85%] transform -translate-y-1/2 left-2" />}
          />
        </div>
        <div>
          <InputWithLogo
            label="Email"
            id="email"
            name="email"
            type="email"
            placeholder="Enter your email"
            onChange={(e) => handleChange(e)}
            formData={formData}
            errors={errors}
            leftIcon={<FiInbox className="pointer-events-none w-5 h-5 absolute top-[85%] transform -translate-y-1/2 left-2" />}
          />
        </div>
        <div>
          <InputWithLogo
            label="Confirm Email"
            id="confirm_email"
            name="confirm_email"
            type="email"
            placeholder="Enter your confirm email"
            onChange={(e) => handleChange(e)}
            formData={formData}
            errors={errors}
            leftIcon={<FiInbox className="pointer-events-none w-5 h-5 absolute top-[85%] transform -translate-y-1/2 left-2" />}
          />
        </div>
        <div>
          <InputWithLogo
            label="Password"
            id="password"
            name="password"
            type={passwordIsVisible ? 'password' : 'text'}
            placeholder="Enter your password"
            onChange={(e) => handleChange(e)}
            formData={formData}
            errors={errors}
            leftIcon={<FiLock className="pointer-events-none w-5 h-5 absolute top-[85%] transform -translate-y-1/2 left-2" />}
            passwordIsVisible={passwordIsVisible}
            setPasswordVisible={setPasswordVisible}
          />
        </div>
        <div>
          <InputWithLogo
            label="Confirm Password"
            id="confirm_password"
            name="confirm_password"
            type={confirmPasswordIsVisible ? 'password' : 'text'}
            placeholder="Enter your password"
            onChange={(e) => handleChange(e)}
            formData={formData}
            errors={errors}
            leftIcon={<FiLock className="pointer-events-none w-5 h-5 absolute top-[85%] transform -translate-y-1/2 left-2" />}
            confirmPasswordIsVisible={confirmPasswordIsVisible}
            setConfirmPasswordVisible={setConfirmPasswordVisible}
          />
        </div>
      </div>
      <Button loading={isLoading} type="submit" variant="filled" className="bg-primary-500 flex justify-center">Sign Up</Button>
    </form>
  );
};