import * as React from 'react';
import { Button } from '@material-tailwind/react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import { useGoogleLogin } from '@react-oauth/google';
import { rc4_encrypt_object, rc4_decrypt_object } from '../../../utils/encryptions';
import apiService from '../../../utils/services';
import sessions from '../../../utils/sessions';
import googleLogo from '../assets/images/logo/google.png';

export default function ButtonLoginWithGoogle() {
  const navigate = useNavigate();
  const location = useLocation();
  const { executeRecaptcha } = useGoogleReCaptcha();

  React.useEffect(() => {
    // token && navigate('/dashboard');
  }, []);

  const onLoginWithGoogle = useGoogleLogin({
    onSuccess: async tokenResponse => {
      try {
        const response = await fetch("https://www.googleapis.com/oauth2/v3/userinfo", {
          method: "POST",
          headers: { Authorization: `Bearer ${tokenResponse.access_token}` },
        });
        const resultLoginGoogle = await response.json();
        if (resultLoginGoogle.email) {
          if (!executeRecaptcha) {
            console.log("Execute recaptcha not yet available");
            return;
          };
          executeRecaptcha("enquiryFormSubmit")
            .then(async (gRecaptchaToken) => {
              const resultRecaptcha: any = await onLogin(gRecaptchaToken, resultLoginGoogle.email, resultLoginGoogle.name);
              // if (resultRecaptcha.status == true) {
              //     iToastify(resultRecaptcha.message, 'success');
              //     const { route } = router.query;
              //     const newRoute = (route) ? "&route=" + route : ""
              //     router.push(process.env.NEXT_PUBLIC_USER_URL + "/login?token=" + resultRecaptcha.token + newRoute + "&gRecaptchaToken=" + gRecaptchaToken + "&provider=google");
              //   } else {
              //     iToastify(resultRecaptcha.message, 'error');
              //   };
            });
        };
      } catch (error) {
        console.error('Error:', error);
      };
    }
  });

  const onLogin = (async (gReCaptchaToken: string, email: string, name: string) => {
    try {
      const response = await fetch(process.env.NEXT_PUBLIC_API_URL + "/auth/auth_check", {
        method: "POST",
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          provider: "google",
          email: email,
          name: name,
          gRecaptchaToken: gReCaptchaToken,
          // hostKey: Base64.encode(window.location.origin),
        }),
      });
      const result = await response.json();
      if (result.status) {
        localStorage.setItem("userSession", JSON.stringify(result.data));
        localStorage.setItem("userToken", result.token);
        localStorage.setItem("userRole", result.data.user_role[0]);
        // return { status: result.status, message: "Berhasil Masuk! Mohon tunggu..", token: encrypt(result.data.username + "&" + "") };
      } else {
        // iToastify(result.message, 'error');
      };
    } catch (error) {
      console.error('Error:', error);
    };
  });

  return (
    <Button
      variant="outlined"
      onClick={() => onLoginWithGoogle()}
      className="border-gray-500 flex gap-1 justify-center items-center py-2"
    >
      <img src={googleLogo} alt="Google logo" width='24' /> <span>{location.pathname === '/login' ? 'Continue' : 'Register'} with Google</span>
    </Button>
  );
};